$title-Color:#a2a8d3;
$background:#fff;
$body-Background:#e7eaf6;;
$header-Background:#1c4b82;
$border-Color:#ccc;
.icon {
    width: 25px;height: 25px;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
}
* {margin: 0;padding: 0;box-sizing: border-box;}
a{text-decoration: none;padding: 5px;&:hover{color:$title-Color;}}
.high-light{color: #f8a978;border-radius: 5px;background: #dadddf;}
body{
    font-size: 1em;
    background: $body-Background;
    .layout{
        width: 100vw;
        background: $background;
    }
    header{
        padding: 45px 0 30px 0;
        text-align: center;
        position: relative;
        background: $header-Background;
        width: 100%;
        .resume-link{display: none;}
        &>h1{color:#e7eaf6;;text-align: center;}
        &>p{font-size:18px;padding-top: 18px;color:#999;}
        a {text-decoration: none;color: #888;padding: 5px;
            &:hover{background: #333;}}
    }
    aside{
        width: 100%;
        position: relative;
        padding-top: 30px;
        margin-bottom: 30px;
        section{
            margin:20px;
            padding-bottom:15px;
            &:not(:last-of-type){border-bottom:1.5px solid #ccc;} 
            h2{padding-bottom: 10px;color:$title-Color;}
            .personal > h2:first-child{
            padding-bottom: 15px;
            margin-top: -40px;
            .item{font-size: 14px;color:#666;}
        }
        .personal .item {
            & span:first-child{width: 80px;display: inline-block;}
        } 
        .skills .item{
            padding-top: 8px;
            & .keyword{padding-bottom: 6px;}
            & .keywordLength{
                height:5px;
                background: #e9e4e6;
                border-radius: 3px;
                & i{display: block;width: 20px;height: 5px;background: #048998;}
            }
         }
         .contact .item{
            display: flex;
            align-items: center;
            padding: 3px 0px;
            & span{padding-left: 4px;}
         }
         .other .item{
            display: flex;
            align-items: center;
            padding: 3px 0;
            & span{padding-left: 4px;}
         } 
      }   
    }
    main{
        min-height: calc(100vh-198px);
        padding:0 20px;
        h2{
            margin-top: 20px;
            color:$title-Color;
        }
        ul,li{
            list-style: none;
        }
        .item img{
        margin-top: 5px;
        width: 100%;
        border: 4px solid #ddd;
        border-style: dashed;
        border-radius: 5px;
        }
       .item > h3{margin:10px 0;color:$title-Color;}
       .item  li{padding-top:8px;}
       .item p{padding-top: 7px;}  
       .line{
           margin: 10px 70px 10px 35px;
           border:2px solid #ccc;
        }
    }  
    footer{
        text-align: center;
        width: 100%;
        background: $header-Background;
    }
}